$red: #ff0000;
$lred: #ffb8b8;
$blue: #266ff0;
$lblue: #cae5f8;
$dblue: #2a9ced;
$orange: #fe9705;
$green: #3ac430;
$yellow: #0569ff;
$black: #021b44;
$white: #ffffff;
$lwhite: #f9f9f9;
$grey: #ededed;
$twitter: #55acee;
$facebook: #4465b8;
$telegram: #2da4d7;
$rss: #ff9845;

$pfont: 'Georgia', serif;
$sfont: 'Montserrat', sans-serif;

$bshadow: 0 2px 10px 0 rgba($black, .1);
$ashadow: 0 5px 30px -5px rgba($black, .3);

$rygradient: linear-gradient(to top right, rgba($red, .41) 0%, rgba($yellow, .76) 100%);
$bggradient: linear-gradient(to top right, rgba($blue, .49) 0%, rgba($green, .77) 100%);
$wbgradient: linear-gradient(to right, $white 55%, $dblue 45%);

@import './general.scss';
@import './header.scss';
@import './sections.scss';
@import './boxes.scss';
@import './items.scss';
@import './buttons.scss';
@import './social.scss';
@import './text.scss';
@import './article.scss';
